import Link from 'next/link';
import Nav from './Nav';

const Header = () => {
  return (
    <header>
      <Link href="/">
        <a>
          <h2>Medical Malpractice</h2>
        </a>
      </Link>
      <Nav />

      <style jsx>{`
        header {
          display: flex;
          justify-content: space-between;
          flex-flow: wrap;
          background-color: #000000;
          color: #ffffff;
          align-items: center;
        }
        a {
          flex: 1;
          color: #dddddd;
          text-decoration: none;
        }
        a:hover {
          color: #bbbbbb;
        }
        h2 {
          font-size: 22px;
          white-space: nowrap;
          margin: 7px 0 6px;
          text-align: center;
        }
      `}</style>
    </header>
  );
};

export default Header;
